.App {
margin: 0px;
padding: 0px;
}
nav{
  display: flex;
  flex-direction: row;
  text-align: center;
  height: 65px;
  padding :20px;
  
  color: #FFFFFF;  
  background: linear-gradient(90deg, #672280 1.18%, #7F249F 39.06%, #A626D3 100%);
}
.nav--logTitle{
 margin-right:auto;
font-family: 'Karla';
font-style: normal;
font-size: 1.25rem;
}
.nav--logoImg{
height: 100%;
margin-right: 6px;
}
.nav--project{
  font-size: 0.75rem;
  margin-right: 10px;
}
.main{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form{
  margin-top: 10px;
  display: grid;
  grid-template: 80% 80% / 35% 35%;
  gap: 12%;
  justify-content: center;
}
.form--input{
  font-family: 'Karla';
  border-radius: 5px; 
 border: 1px solid #D5D4D8;
 text-indent: 5px;
}
.form--button{
  grid-column: 1 / -1;
  font-family: 'Karla';
  border-radius: 5px;
  background: linear-gradient(90deg, #672280 1.18%, #7F249F 39.06%, #A626D3 100%);
  color: white;
  border: none;
  cursor: pointer;
 max-width: 100%; 
}

.Image-container{
  display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 35px;
}
.mainImage{
 width: 250px;
 height: 320px;
}
